import Section from '../layouts/Section';
import Heading from '../UI/Heading';
import SectionButtons from '../UI/SectionButtons';

import coin from '../../assets/images/cryptocoin.png';
import React, { FC } from 'react';

type Props = {
  ChangeSection: (idx: number) => void;
};

const HomeSection: FC<Props> = React.memo(({ ChangeSection }) => {
  return (
    <Section>
      <div className="home-section">
        <div className="home-section__content content--left">
          <div className="content__text">
            <Heading level={1}></Heading>
            <p className="text">
                      .
            </p>
            <ul className="list">
              <li className="list__item">30  </li>
              <li className="list__item">12     </li>
              <li className="list__item">    24/7</li>
              <li className="list__item">NFT    </li>
            </ul>
            <p className="text">
                       !
            </p>
          </div>
          <div className="buttons">
            <SectionButtons onClick={() => ChangeSection(4)}> </SectionButtons>
            <SectionButtons onClick={() => ChangeSection(5)}>  </SectionButtons>
          </div>
        </div>
        <div className="home-section__content content--right">
          <picture>
            <img src={coin} alt="cryptoCoin" />
          </picture>
        </div>
      </div>
    </Section>
  );
});

export default HomeSection;
